import {
  Component,
  OnInit
} from '@angular/core';
import { CommonService } from 'src/app/services/common.service';
import { Router } from '@angular/router';
import {ModalController} from '@ionic/angular';
import { UserService } from 'src/app/services/user.service';

@Component({
  selector: 'app-new-pin',
  templateUrl: './new-pin.page.html',
  styleUrls: ['./new-pin.page.scss'],
})
export class NewPinPage implements OnInit {

  constructor(
    public commonService: CommonService,
    public userService: UserService,
    private router: Router,
    private modalController: ModalController
  ) {}

  ngOnInit() {}

  Pin: String = "";
  Pin2: String = "";
  ShowPin: Boolean = false;
  ShowPinAgain: Boolean = false;

  async myDismiss(result: any) {
    await this.modalController.dismiss(result);
  }

  eventCapture(event: any) {
    this.ShowPin = false;
    this.ShowPinAgain = true;
    this.Pin = event;
  }

  eventCaptureAgain(event: any) {
    this.ShowPinAgain = false;
    this.Pin2 = event;
    if(this.Pin !== this.Pin2) {
      this.commonService.presentAlert('Pin did not match. Please try again.', 'Oops!', ()=>{
        console.log('failed pin');
      });
    } else {
      const payload = {
        mobile_pin: this.Pin,
        id: this.userService.getUserId()
      }
      this.myDismiss(payload);
    }
  }

  showPin() {
    this.ShowPin = !this.ShowPin;
  }

}