import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Observable, of } from 'rxjs';
import { map } from 'rxjs/operators';
import { environment } from './../../environments/environment';
import { catchError, tap } from 'rxjs/operators';

 
@Injectable({
  providedIn: 'root'
})
export class UserService {
  url = environment.baseUrl;
  apiKey = ''; // <-- Enter your own key here!
  userInfo = JSON.parse(localStorage.getItem('user'));
  /**
   * Constructor of the Service with Dependency Injection
   * @param http The standard Angular HttpClient to make requests
   */
  constructor(private http: HttpClient) { }
 
  checkEmail(param: any) {
    return this.http.post(`${this.url}/check_email`, param)
    .pipe(
      tap(_ => this.log('fetched email')),
      catchError(this.handleError('checkEmail', []))
    );
  }

  setLoginToLocal(data: any) {
    const credentialsKey = 'token';
    const storage = localStorage;
    if (data.token) {
      storage.setItem(credentialsKey, data.token);
      storage.setItem('user', JSON.stringify(data.user));
    } else {
      storage.removeItem(credentialsKey);
      storage.removeItem('user');
    }
  }

  setUserToLocal(data: any) {
    if (data) {
      localStorage.setItem('user', JSON.stringify(data));
    }
  }

  setTokenToLocal(data: any) {
    if (data) {
      localStorage.setItem('token', data.token);
    }
  }

  getUserInfo() {
    const storage = localStorage;
    const user = JSON.parse(storage.getItem('user'));
    return user;
  }

  getUserId() {
    const user = this.getUserInfo();
    return user && user.id;
  }
 
  getUsers(param: any) {
    return this.http.get(`${this.url}/contacts`, {params: param });
  }
 
  updateUser(payload: any) {
    return this.http.put(`${this.url}/contacts/${payload.id}`, payload)
    .pipe(
      tap(_ => this.log('fetched contacts')),
      catchError(this.handleError('contacts', []))
    );
  }
 
  updatePassword(payload: any) {
    return this.http.put(`${this.url}/contact/password/${payload.id}`, payload)
    .pipe(
      tap(_ => this.log('fetched contacts')),
      catchError(this.handleError('contacts', []))
    );
  }
 
  updatePin(payload: any) {
    return this.http.put(`${this.url}/contact/pin/${payload.id}`, payload)
    .pipe(
      tap(_ => this.log('fetched contacts')),
      catchError(this.handleError('contacts', []))
    );
  }
 
  updateAlert(payload: any) {
    return this.http.put(`${this.url}/contact/alert/${payload.id}`, payload)
    .pipe(
      tap(_ => this.log('fetched contacts')),
      catchError(this.handleError('contacts', []))
    );
  }
 
  login(param: any) {
    return this.http.post(`${this.url}/login_mobile`, param)
    .pipe(
      tap(_ => this.log('fetched email')),
      catchError(this.handleError('login', []))
    );
  }
 
  logout() {
    return this.http.post(`${this.url}/logout`, {})
    .pipe(
      tap(_ => this.log('fetched email')),
      catchError(this.handleError('logout', []))
    );
  }
 
  resetPassword(param: any) {
    return this.http.post(`${this.url}/forgot/password`, param)
    .pipe(
      tap(_ => this.log('fetched email')),
      catchError(this.handleError('forgot', []))
    );
  }
 
  submitTicket(param: any) {
    return this.http.post(`${this.url}/support`, param)
    .pipe(
      tap(_ => this.log('fetched email')),
      catchError(this.handleError('forgot', []))
    );
  }
 
  refreshToken() {
    return this.http.post(`${this.url}/refresh`, {})
    .pipe(
      tap(_ => this.log('fetched email')),
      catchError(this.handleError('logout', []))
    );
  }

  handleError<T> (operation = 'operation', result?: T) {
    return (error: any): Observable<T> => {

      // TODO: send the error to remote logging infrastructure
      console.error(error); // log to console instead

      // TODO: better job of transforming error for user consumption
      this.log(`${operation} failed: ${error.message}`);

      // Let the app keep running by returning an empty result.
      return of(result as T);
    };
  }

  /** Log a HeroService message with the MessageService */
  log(message: string) {
    console.log(message);
  }

}
