import { NgModule } from '@angular/core';
import { PreloadAllModules, RouterModule, Routes } from '@angular/router';

const routes: Routes = [
  { path: '', loadChildren: './tabs/tabs.module#TabsPageModule' },
  { path: 'tab4', loadChildren: './tab4/tab4.module#Tab4PageModule' },
  { path: 'login', loadChildren: './login/login.module#LoginPageModule' },
  { path: 'signup', loadChildren: './signup/signup.module#SignupPageModule' },
  { path: 'password', loadChildren: './password/password.module#PasswordPageModule' },
  { path: 'remarks', loadChildren: './cases/modal/remarks/remarks.module#RemarksPageModule' },
  { path: 'tab0', loadChildren: './tab0/tab0.module#Tab0PageModule' },
  { path: 'support', loadChildren: './dashboard/support/support.module#SupportPageModule' },
  { path: 'faq', loadChildren: './dashboard/faq/faq.module#FaqPageModule' },
  { path: 'new-pin', loadChildren: './modal/new-pin/new-pin.module#NewPinPageModule' },
  { path: 'lock-screen', loadChildren: './lock-screen/lock-screen.module#LockScreenPageModule' },
  { path: 'lock-screen2', loadChildren: './lock-screen/lock-screen.module#LockScreenPageModule' },
  { path: 'pin-screen', loadChildren: './modal/pin-screen/pin-screen.module#PinScreenPageModule' },  { path: 'forgot-password', loadChildren: './forgot-password/forgot-password.module#ForgotPasswordPageModule' },
  { path: 'support-form', loadChildren: './dashboard/support-form/support-form.module#SupportFormPageModule' },
  { path: 'notifications', loadChildren: './dashboard/notifications/notifications.module#NotificationsPageModule' },

];
@NgModule({
  imports: [
    RouterModule.forRoot(routes, { preloadingStrategy: PreloadAllModules })
  ],
  exports: [RouterModule]
})
export class AppRoutingModule {}
