import { Component, OnInit, Input } from '@angular/core';
import { PopoverController } from '@ionic/angular';
import { PopUpUserComponent } from '../pop-up-user/pop-up-user.component';


@Component({
  selector: 'app-pop-up-list',
  templateUrl: './pop-up-list.component.html',
  styleUrls: ['./pop-up-list.component.scss'],
})
export class PopUpListComponent implements OnInit {
  @Input() contacts: any = "Enter Pin";

  constructor(
    public popoverController: PopoverController
  ) { }

  ngOnInit() {
    console.log(this.contacts);
  }

  async dismissClick() {
    await this.popoverController.dismiss();
  }

  async presentPopover(contact: any) {
    this.dismissClick();
    const popover = await this.popoverController.create({
      component: PopUpUserComponent,
      // event: ev,
      animated: true,
      showBackdrop: true,
      componentProps: {
        contact
      }
    });
    return await popover.present();
  }

}
