import { Component, OnInit } from '@angular/core';
import { UserService } from '../services/user.service';
import {
  Router
} from '@angular/router';
import { CommonService } from '../services/common.service';

@Component({
  selector: 'app-lock-screen',
  templateUrl: './lock-screen.page.html',
  styleUrls: ['./lock-screen.page.scss'],
})
export class LockScreenPage implements OnInit {
  userInfo: any = {};
  showNow: boolean = false;
  constructor(
    private router: Router,
    private commonService: CommonService,
    public userService: UserService
  ) { }

  ngOnInit() {
    this.userInfo = this.userService.getUserInfo();
  }

  eventCapture(event: any) {
    if (this.userInfo && parseInt(this.userInfo.mobile_pin, 10) === parseInt(event, 10)) {
      this.commonService.presentLoading();

      this.userService.login(this.userInfo)
      .subscribe((res: any) => {
        this.commonService.loadingBox.dismiss();
        if (res.error) {
          this.commonService.presentToastWithOptions(res.error);
        } else {
          this.userService.setLoginToLocal(res);
          this.router.navigate(['/tabs/tab1']);
        }
      });
    } else {
      this.commonService.presentToastWithOptions('Pin entered is incorrect.');
    }
  }

  onLogout() {
    this.commonService.logoutAlert();
  }

}
