import { Injectable } from '@angular/core';
import { ToastController, AlertController, LoadingController, ModalController } from '@ionic/angular';
import { UserService } from './user.service';
import { Router } from '@angular/router';
import { HttpClient } from '@angular/common/http';
import { environment } from 'src/environments/environment';

@Injectable({
  providedIn: 'root'
})
export class CommonService {
  intervalTime = 3000;
  url = environment.baseUrl;

  loadingBox: any;
  constructor(
    private http: HttpClient,
    private router: Router,
    private userService: UserService,
    private modalController: ModalController,
    private alertController: AlertController,
    private loadingController: LoadingController,
    private toastController: ToastController
  ) { }

  async presentLoading() {
    this.loadingBox = await this.loadingController.create({
      message: 'Loading',
      // duration: 2000,
      showBackdrop: true,
      // spinner: 'circles'
    });
    await this.loadingBox.present();
  }

  stopLoading() {
    this.loadingBox && this.loadingBox.dismiss();

    if (!this.loadingBox) {
      let terval  = setInterval(() => {
        if (this.loadingBox) {
          this.loadingBox.dismiss();
          clearInterval(terval);
        }
      }, 5);
    }
  }

  async presentAlert(message: string, header: string, callback: any) {
    const alert = await this.alertController.create({
      header: header || 'Hey!',
      message,
      buttons: [
        {
          text: 'OK',
          role: 'cancel',
          cssClass: 'success',
          handler: callback && callback()
        }
      ]
    });
    await alert.present();
  }

  
  async presentToastWithOptions(message: string) {
    const toast = await this.toastController.create({
      message: message || 'Your settings have been saved.',
      showCloseButton: true,
      position: 'top',
      duration: 2000,
      closeButtonText: 'Close'
    });
    toast.present();
  }


  async logoutAlert(message: string = 'Are you sure you want to logout now?') {
    const alert = await this.alertController.create({
      header: 'Logout',
      message,
      buttons: [
        {
          text: 'Cancel',
          role: 'cancel',
          cssClass: 'success',
          handler: (blah) => {
            console.log('Confirm Cancel: blah');
          }
        }, {
          text: 'Logout',
          handler: () => {
            this.presentLoading();
            console.log('Confirm Okay');
            this.userService.logout()
            .subscribe(() => {
              this.loadingBox.dismiss();
              this.userService.setLoginToLocal({});
              this.router.navigate(['/login'], { replaceUrl: true });
            });
          }
        }
      ]
    });
    await alert.present();
  }

  getNotifications(param: any) {
    return this.http.get(`${this.url}/notification`, {params: param});
  }

  refreshToken() {
    this.userService.refreshToken()
    .subscribe((res: any) => {
      this.userService.setTokenToLocal(res);
    });
  }
}
