import { Component, OnInit, Input } from '@angular/core';
import { PopoverController } from '@ionic/angular';
import { Router } from '@angular/router';
import * as moment from 'moment-timezone';

@Component({
  selector: 'app-pop-up-user',
  templateUrl: './pop-up-user.component.html',
  styleUrls: ['./pop-up-user.component.scss'],
})
export class PopUpUserComponent implements OnInit {
  @Input() contact: any = {};
  time: any;

  constructor(
    public router: Router,
    public popoverController: PopoverController
  ) {
    setInterval(() => {
      const ttime = new Date();
      this.time = moment(ttime).tz(this.contact.timezone);
  }, 1000) // Updates the time every second.

  }

  ngOnInit() {
    console.log(this.contact, 'contaact');
  }

  async dismissClick() {
    this.router.navigate([`/tabs/tab2/messages/conversation/${this.contact.room_id}`]);
    await this.popoverController.dismiss();
  }

}
